import React, { lazy, Suspense } from "react";
import { Route, useLocation, Switch } from "react-router-dom";
const LOGIN = lazy(() => import("./components/login/LOGIN"));
const WELCOME = lazy(() => import("./components/welcome/Welcome"));
const RESETPASSWORD_1 = lazy(() =>
  import("./components/PaswordReset/Password-Reset/PasswordReset")
);
const RESETPASSWORD_2 = lazy(() =>
  import("./components/PaswordReset/Reseted-Password/PasswordReset")
);
const RESETPASSWORDFORM = lazy(() =>
  import("./components/PaswordReset/Password-Reset-Form/PasswordResetForm")
);
const SIGNUP_MAIN = lazy(() => import("./components/signup/SignUp"));
function App() {
  const location = useLocation();
  return (
    <Suspense fallback={null}>
      <Switch location={location}>
        <Route exact path="/login" component={LOGIN} />
        <Route exact path="/" component={WELCOME} />
        <Route exact path="/password-reset" component={RESETPASSWORD_1} />
        <Route exact path="/password-reseted" component={RESETPASSWORD_2} />
        <Route exact path="/SignUp" component={SIGNUP_MAIN} />
        <Route
          exact
          path="/password-reset-form"
          component={RESETPASSWORDFORM}
        />
      </Switch>
    </Suspense>
  );
}
export default App;
